import React, { useEffect, useState } from "react";
import styled from "styled-components";
import { Div, Text, Button, Icon } from "atomize";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate, Link } from "react-router-dom";
import LoadingPage from "../LoadingPage";
import {
  getBusinesses,
  selectBusinesses,
  selectIsLoading,
  selectTotalPages,
  cleanBusinesses,
} from "../../redux/reducers/businessReducer";
import { selectUser } from "../../redux/reducers/userReducer";
import PaginationButton from "../../Components/PaginationButton";
import SuccessNotification from "../../Components/Notifications/SuccessNotification";
import WarningNotification from "../../Components/Notifications/WarningNotification";

const Table = styled.table`
  padding: 2rem;
  border: 1px solid;
  border-radius: 5px;
`;

const Td = styled.td`
  border: 1px solid;
  padding: 1rem;
`;

const BusinessEditButton = styled(Link)`
  text-decoration: none;
`;

const AddBusiness = styled(Link)`
  cursor: pointer;
  margin-left: 1rem;
`;

export default function BusinessesPage() {
  const businesses = useSelector(selectBusinesses);
  const totalPages = useSelector(selectTotalPages);
  const isLoading = useSelector(selectIsLoading);
  const user = useSelector(selectUser);
  const [page, setPage] = useState(1);
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const limit = 10;

  useEffect(() => {
    if (user === "non-login") {
      return navigate("/");
    }
    window.scroll(0, 0);
    dispatch(
      getBusinesses({
        limit,
        page,
      })
    );
    return () => {
      dispatch(cleanBusinesses());
    };
  }, [user, page, dispatch]);

  return (
    <Div
      w="90%"
      minH={isLoading ? "150rem" : "50rem"}
      m={{ y: "4rem", x: "auto" }}
      p={{ xs: "1rem", lg: "3rem", b: { lg: "6rem" } }}
      pos="relative"
    >
      {isLoading && <LoadingPage />}
      <Div>
        <Div d="flex" m={{ b: "1rem" }}>
          <Text textSize="display1">生意攤資料</Text>
          <AddBusiness to={`/business`}>
            <Icon name="Add" size="40px" color="info600" />
          </AddBusiness>
        </Div>
        <Div>
          <Div d="flex" justify="center">
            <Table>
              <tr>
                <Td>ID</Td>
                <Td>電話</Td>
                <Td>手機</Td>
                <Td>店名</Td>
                <Td>地址</Td>
                <Td>備註</Td>
                <Td>價格</Td>
                <Td>／</Td>
              </tr>

              {businesses.map((business) => (
                <tr key={business.id}>
                  <Td>{business && business.id}</Td>
                  <Td>{business && business.telephone}</Td>
                  <Td>{business && business.cellphone}</Td>
                  <Td>{business && business.store_name}</Td>
                  <Td>{business && business.store_address}</Td>
                  <Td>{business && business.remark}</Td>
                  <Td>{business && business.price}</Td>
                  <Td>
                    <BusinessEditButton
                      to={`/businesses/update/${business.id}`}
                    >
                      <Button
                        h="2.5rem"
                        p={{ x: "1rem" }}
                        textSize="body"
                        textColor="info800"
                        textWeight="600"
                        hoverTextColor="info900"
                        bg="info300"
                        hoverBg="info400"
                        m={{ l: "0.5rem" }}
                      >
                        編輯
                      </Button>
                    </BusinessEditButton>
                  </Td>
                </tr>
              ))}
            </Table>
          </Div>
        </Div>
      </Div>
      <PaginationButton totalPages={totalPages} page={page} setPage={setPage} />
      <SuccessNotification />
      <WarningNotification />
    </Div>
  );
}
