import React, { useEffect } from "react";
import { useParams } from "react-router";
import styled from "styled-components";
import { Div, Text, Button } from "atomize";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate, Link } from "react-router-dom";
import LoadingPage from "../LoadingPage";
import {
  searchMembers,
  selectSearchedMembers,
  selectIsLoading,
  cleanSearchMembers,
} from "../../redux/reducers/memberReducer";
import { selectUser } from "../../redux/reducers/userReducer";
import SuccessNotification from "../../Components/Notifications/SuccessNotification";
import WarningNotification from "../../Components/Notifications/WarningNotification";

const Table = styled.table`
  padding: 2rem;
  border: 1px solid;
  border-radius: 5px;
`;

const Td = styled.td`
  border: 1px solid;
  padding: 1rem;
`;

const MemberEditButton = styled(Link)`
  text-decoration: none;
`;

export default function MembersPage() {
  let { keyword } = useParams();
  const searchedMembers = useSelector(selectSearchedMembers);
  const isLoading = useSelector(selectIsLoading);
  const user = useSelector(selectUser);
  const dispatch = useDispatch();
  const navigate = useNavigate();

  useEffect(() => {
    if (user === "non-login") {
      return navigate("/");
    }
    window.scroll(0, 0);
    dispatch(searchMembers(keyword));
    window.scrollTo({
      top: 0,
      left: 0,
      behavior: "instant",
    });
    return () => {
      dispatch(cleanSearchMembers());
    };
  }, [keyword, user, dispatch]);

  return (
    <Div
      w="80%"
      minH={isLoading ? "150rem" : "50rem"}
      m={{ y: "4rem", x: "auto" }}
      p={{ xs: "1rem", lg: "3rem", b: { lg: "6rem" } }}
      position="relative"
    >
      {isLoading && <LoadingPage />}
      <Div>
        <Text textSize="display1">會員資料</Text>

        <Div>
          <Div d="flex" justify="center">
            <Table>
              <tr>
                <Td>ID</Td>
                <Td>要求</Td>
                <Td>電話</Td>
                <Td>手機</Td>
                <Td>地址</Td>
                <Td>備註</Td>
                <Td>瓦斯頻率</Td>
                <Td>／</Td>
              </tr>

              {searchedMembers &&
                searchedMembers.map((searchedmember) => (
                  <tr key={searchedmember.id}>
                    <Td>{searchedmember && searchedmember.id}</Td>
                    <Td>
                      <Div
                        textColor="brand800"
                        textSize="display1"
                        justify="center"
                        align="center"
                      >
                        {searchedmember && searchedmember.isRequest ? "＊" : ""}
                      </Div>
                    </Td>
                    <Td>{searchedmember && searchedmember.telephone}</Td>
                    <Td>{searchedmember && searchedmember.cellphone}</Td>
                    <Td>{searchedmember && searchedmember.address}</Td>
                    <Td>{searchedmember && searchedmember.remark}</Td>
                    <Td>{searchedmember && searchedmember.frequency}</Td>
                    <Td>
                      <MemberEditButton
                        to={`/members/update/${searchedmember.id}`}
                      >
                        <Button
                          h="2.5rem"
                          p={{ x: "1rem" }}
                          textSize="body"
                          textColor="info800"
                          textWeight="600"
                          hoverTextColor="info900"
                          bg="info300"
                          hoverBg="info400"
                          m={{ l: "0.5rem" }}
                        >
                          編輯
                        </Button>
                      </MemberEditButton>
                    </Td>
                  </tr>
                ))}
            </Table>
          </Div>
        </Div>
      </Div>
      <SuccessNotification />
      <WarningNotification />
    </Div>
  );
}
