import React, { useEffect, useState } from "react";
import styled from "styled-components";
import { Div, Text, Button, Icon } from "atomize";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate, Link } from "react-router-dom";
import LoadingPage from "../LoadingPage";
import {
  getMembers,
  selectMembers,
  selectIsLoading,
  selectTotalPages,
  cleanMembers,
} from "../../redux/reducers/memberReducer";
import { selectUser } from "../../redux/reducers/userReducer";
import PaginationButton from "../../Components/PaginationButton";
import SuccessNotification from "../../Components/Notifications/SuccessNotification";
import WarningNotification from "../../Components/Notifications/WarningNotification";

const Table = styled.table`
  padding: 2rem;
  border: 1px solid;
  border-radius: 5px;
`;

const Td = styled.td`
  border: 1px solid;
  padding: 1rem;
`;

const MemberEditButton = styled(Link)`
  text-decoration: none;
`;

const AddMember = styled(Link)`
  cursor: pointer;
  margin-left: 1rem;
`;

export default function MembersPage() {
  const members = useSelector(selectMembers);
  const totalPages = useSelector(selectTotalPages);
  const isLoading = useSelector(selectIsLoading);
  const user = useSelector(selectUser);
  const [page, setPage] = useState(1);
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const limit = 10;

  useEffect(() => {
    if (user === "non-login") {
      return navigate("/");
    }
    window.scroll(0, 0);
    dispatch(
      getMembers({
        limit,
        page,
      })
    );
    return () => {
      dispatch(cleanMembers());
    };
  }, [user, page, dispatch]);

  return (
    <Div
      w="80%"
      minH={isLoading ? "150rem" : "60rem"}
      m={{ y: "4rem", x: "auto" }}
      p={{ xs: "1rem", lg: "3rem", b: { lg: "6rem" } }}
      pos="relative"
    >
      {isLoading && <LoadingPage />}
      <Div>
        <Div d="flex" m={{ b: "1rem" }}>
          <Text textSize="display1">會員資料列表</Text>
          <AddMember to={`/member`}>
            <Icon name="Add" size="40px" color="info600" />
          </AddMember>
        </Div>
        <Div>
          <Div d="flex" justify="center">
            <Table>
              <tr>
                <Td>ID</Td>
                <Td>要求</Td>
                <Td>電話</Td>
                <Td>手機</Td>
                <Td>地址</Td>
                <Td>備註</Td>
                <Td>頻率</Td>
                <Td>／</Td>
              </tr>

              {members.map((member) => (
                <tr key={member.id}>
                  <Td>{member && member.id}</Td>
                  <Td>
                    <Div
                      textColor="brand800"
                      textSize="display1"
                      justify="center"
                      align="center"
                    >
                      {member && member.isRequest ? "＊" : ""}
                    </Div>
                  </Td>
                  <Td>{member && member.telephone}</Td>
                  <Td>{member && member.cellphone}</Td>
                  <Td>{member && member.address}</Td>
                  <Td>{member && member.remark}</Td>
                  <Td>{member && member.frequency}</Td>
                  <Td>
                    <MemberEditButton to={`/members/update/${member.id}`}>
                      <Button
                        h="2.5rem"
                        p={{ x: "1rem" }}
                        textSize="body"
                        textColor="info800"
                        textWeight="600"
                        hoverTextColor="info900"
                        bg="info300"
                        hoverBg="info400"
                        m={{ l: "0.5rem" }}
                      >
                        編輯
                      </Button>
                    </MemberEditButton>
                  </Td>
                </tr>
              ))}
            </Table>
          </Div>
        </Div>
      </Div>
      <PaginationButton totalPages={totalPages} page={page} setPage={setPage} />
      <SuccessNotification />
      <WarningNotification />
    </Div>
  );
}
