import React, { useState } from "react";
import PropTypes from "prop-types";
import { Div, Anchor, Input, Icon } from "atomize";
import { useNavigate } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import { logout } from "../redux/reducers/userReducer";
import { selectUser } from "../redux/reducers/userReducer";
import SyncSuccessNotification from "./Notifications/SyncSuccessNotification";

const SearchBar = ({ searchContent, setSearchContent, search }) => {
  return (
    <Div d="flex" align="center">
      <Input
        w={{ xs: "23rem", md: "18rem" }}
        value={searchContent}
        onChange={(e) => {
          setSearchContent(e.target.value);
        }}
        onKeyPress={(e) => e.key === "Enter" && search()}
        placeholder="請輸入電話或地址"
        suffix={
          <Icon
            name="Search"
            size="20px"
            cursor="pointer"
            onClick={() => search()}
            pos="absolute"
            top="25%"
            right="1rem"
          />
        }
      />
    </Div>
  );
};

SearchBar.propTypes = {
  searchContent: PropTypes.string,
  setSearchContent: PropTypes.func,
  search: PropTypes.func,
};

const Button = ({ name, link, logoutEvent }) => {
  return (
    <Div w={{ xs: "50%", sm: "25%" }} textAlign="center">
      <Anchor
        href={link}
        h="2.5rem"
        p={{ x: "1.25rem" }}
        textSize="subheader"
        bg="transparent"
        textColor="info800"
        textWeight="600"
        hoverTextColor="warning800"
        onClick={() => name === "登出" && logoutEvent()}
      >
        {name}
      </Anchor>
    </Div>
  );
};

Button.propTypes = {
  name: PropTypes.string,
  link: PropTypes.string,
  logoutEvent: PropTypes.func,
};

const NavbarButtons = () => {
  return (
    <Div
      d="flex"
      flexDir={{ xs: "column", md: "row" }}
      justify={{ xs: "center" }}
      align="center"
      w="30rem"
      m={{ t: "1rem" }}
      transform="translate(-3rem)"
    >
      <Button name="會員資料" link="#/members" />
      <Button name="商家資料" link="#/businesses" />
      <Button name="聯絡資訊" link="#/contact" />
    </Div>
  );
};

const UserButtons = ({ user, logoutEvent }) => {
  return (
    <Div
      d="flex"
      flexDir={{ sm: "column", md: "row" }}
      justify={{ xs: "center" }}
      align="center"
      w="30rem"
      transform="translate(-3rem)"
      m={{ t: "1rem" }}
    >
      {user && user === "non-login" && <Button name="登入" link="#/signin" />}
      {user && user !== "non-login" && (
        <Button name="登出" logoutEvent={logoutEvent} />
      )}
    </Div>
  );
};

UserButtons.propTypes = {
  user: PropTypes.object,
  logoutEvent: PropTypes.func,
};

const Navbar = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const [searchContent, setSearchContent] = useState("");
  const [showSuccess, setShowSuccess] = useState(false);
  const user = useSelector(selectUser);

  const logoutEvent = () => {
    dispatch(logout());
    setShowSuccess(true);
    navigate(`/`);
  };

  const search = () => {
    if (!searchContent) {
      return;
    }
    navigate(`/members/search/${searchContent}`);
    setSearchContent("");
  };

  return (
    <Div
      d={{ xs: "block", md: "flex" }}
      tag="navbar"
      m={{ t: "1.5rem", b: "3rem", x: "1.5rem" }}
      p={{ y: "1.5rem", x: "2rem" }}
      align="center"
      justify="space-between"
      border={{ b: "2px solid" }}
      bg="warning500"
      borderColor="warning800"
      shadow="3"
    >
      <Div>
        <SearchBar
          searchContent={searchContent}
          setSearchContent={setSearchContent}
          search={search}
        />
      </Div>
      <NavbarButtons />
      <UserButtons user={user} logoutEvent={logoutEvent} />
      <SyncSuccessNotification
        showSuccess={showSuccess}
        setShowSuccess={setShowSuccess}
        successMessage={`已登出`}
      />
    </Div>
  );
};

export default Navbar;
